<template>
	<box-item title="经营主体统计">
		<div class="panel-body">
			<div class="chart-panel">
				<div class="chart-wrap">
					<div class="echart-container" ref="EContainer"></div>
					<div class="chart-content">
						<p class="chart-title">经营主体</p>
						<p class="chart-sub-title">{{ totalNum }}</p>
						<p class="chart-sub-unit">（家）</p>
					</div>
				</div>
				<div class="chart-legend-wrap">
					<ul class="chart-legend">
						<li class="legend-item" v-for="(legend, index) in chartData" :key="index">
							<i class="dot" :style="{backgroundColor: legend.color}"></i>
							<p class="legend">
								{{ legend.name }}：<i class="legend-value">{{ legend.value }}</i
								>家
							</p>
						</li>
					</ul>
				</div>
			</div>
			<div class="bottom-info-wrap">
				<div class="bottom-info">
					入网经营主体数：<span class="num">{{ mainBodyInTheSystem }}</span
					><span class="unit">家</span>
				</div>
			</div>
		</div>
	</box-item>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
import countTo from '@/components/common/countTo';

import {areaLevelCorAreaId, maxDecimalPlaces} from '@/common/fn';
import {getStorage} from '@/utils/storage';

const govGovernmentSubjectVO = getStorage('govGovernmentSubjectVO');
const color = ['#D5EF10', '#F6A82B', '#32CEBB', '#10AC2A', '#3649FB', '#CB4A99'];
// echarts 超级变量含义 {a} series.name, {b} data.name {c} data.value {d} data%
const pieChartOption = {
	title: {
		show: false,
		// subtext: '',
		left: 'center',
		top: '35%',
		textStyle: {
			color: '#9ED2D8',
			fontSize: 12,
			lineHeight: 22
		},
		subtextStyle: {
			color: '#00F6FF',
			fontSize: 24,
			lineHeight: 34,
			fontWeight: 'bold'
		},
		itemGap: 0
	},
	tooltip: {
		show: true,
		trigger: 'item',
		formatter: '{b}：{c}家',
		backgroundColor: '#043250',
		padding: [13, 14, 13, 11],
		textStyle: {
			fontSize: 12,
			color: '#9ED2D8'
		}
	},
	// , '#F087B1', '#46F262', '#9CE9E8', '#BB97FB'
	color,
	textStyle: {
		color: '#9DD1D7',
		fontSize: 14,
		fontWeight: '400',
		lineHeight: 14
	},
	series: [
		{
			name: '经营主体类型统计',
			type: 'pie',
			// center: ['50%', '80%'],
			radius: ['76%', '88%'],
			avoidLabelOverlap: true,
			minAngle: 15,
			startAngle: 80,
			label: {
				show: false,
				normal: {
					show: false,
					fontSize: 12,
					position: 'outside',
					lineHeight: 16,
					formatter: ['{nameS|{b}}', '{valueS|{c}家}'].join('：'),
					// formatter: '{nameS|{b}}：{valueS|{c}家}',
					rich: {
						nameS: {
							color: '#9ED2D8'
						},
						valueS: {
							color: '#00F6FF'
						}
					}
				}
			},
			labelLine: {
				show: false,
				normal: {
					show: false,
					length: 20,
					length2: 10
					// smooth: 0
				}
			},
			hoverAnimation: true,
			selectedMode: 'multiple',
			selectedOffset: 2,
			data: []
		},
		{
			type: 'pie',
			radius: ['69%', '70%'],
			minAngle: 15,
			startAngle: 80,
			animation: true,
			labelLine: {
				show: false
			},
			hoverAnimation: false,
			z: 3,
			tooltip: {
				show: false
			},
			label: {
				show: false
			},
			selectedMode: 'multiple',
			selectedOffset: 4,
			data: []
		}
	]
};
export default {
	name: 'OperatorsNum',
	props: {
		// 入网经营主体数(系统中的主体)
		mainBodyInTheSystem: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			chart: null,
			chartData: [],
			totalNum: 17958
		};
	},
	mounted() {
		this.initPage();
		window.addEventListener('resize', this.resizeChart, false);
	},
	methods: {
		initPage() {
			this.chart = this._initEcharts(this.eContainerRef, pieChartOption);
			this.renderChart();
		},
		_initEcharts(el, option) {
			const chart = this.$echarts.init(el);
			chart.setOption(option);
			return chart;
		},
		// 渲染charts
		renderChart() {
			const arr = [
				{
					name: '合作社',
					value: 1054
				},
				{
					name: '家庭农场',
					value: 569
				},
				{
					name: '农业企业',
					value: 316
				},
				{
					name: '大户',
					value: 2690
				},
				{
					name: '散户',
					value: 12606
				},
				{
					name: '村组集体',
					value: 723
				}
			];
			this.chartData = arr.map((item, index) => ({...item, color: color[index]}));
			/*this.chartData = (() => {
				let arr = [];
				for (let i = 0; i < 6; i++) {
					arr.push({
						name: `test${i}`,
						value: Math.round(Math.random() * 10000),
						color: color[i]
					});
				}
				return arr;
			})();*/
			this.chart.setOption({
				series: [
					{
						data: this.chartData
					},
					{
						data: this.chartData
					}
				]
			});
		},
		resizeChart() {
			if (this.chart) {
				this.chart.resize();
			}
		}
	},
	computed: {
		eContainerRef() {
			return this.$refs.EContainer;
		}
	},
	components: {
		BoxItem,
		countTo
	}
};
</script>

<style scoped lang="less">
.panel-body {
	width: 100%;
	height: 100%;
	position: relative;
	//display: flex;
	//justify-content: space-between;
	//align-items: center;
	.chart-panel {
		width: 100%;
		height: 80%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		.chart-wrap {
			flex: 1;
			width: 50%;
			max-width: 50%;
			height: 100%;
			position: relative;

			.echart-container {
				width: 100%;
				height: 100%;
			}

			.chart-content {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				white-space: nowrap;
				text-align: center;

				.chart-title {
					font-size: 16px;
					color: #9dd1d7;
					height: 26px;
					line-height: 26px;
				}

				.chart-sub-title {
					font-size: 30px;
					font-weight: bold;
					color: #feeb48;
					height: 40px;
					line-height: 40px;
				}

				.chart-sub-unit {
					font-size: 12px;
					color: #feeb48;
					height: 16px;
					line-height: 16px;
				}
			}
		}

		/* 图例 */
		.chart-legend-wrap {
			flex: 1;
			width: 50%;
			max-width: 50%;
			height: 100%;
			display: flex;
			align-items: center;

			.chart-legend {
				padding-left: 15px;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: space-around;

				.legend-item {
					height: 24px;
					line-height: 24px;

					.dot {
						width: 8px;
						height: 8px;
						border-radius: 4px;
						margin-right: 6px;
						display: inline-block;
					}

					.legend {
						display: inline-block;
						font-size: 14px;
						color: #9dd1d7;

						.legend-value {
							font-style: normal;
							font-weight: bold;
						}
					}

					+ .legend-item {
						/*margin-top: 6px;*/
					}
				}
			}
		}
	}
	.bottom-info-wrap {
		height: 20%;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 82%;
		border-top: 1px solid #0e5b7c;
		margin: 0 auto;
		.bottom-info {
			font-size: 16px;
			height: 26px;
			line-height: 26px;
			font-weight: bold;
			color: #057486;
			text-align: center;
			span {
				color: #00f6ff;
				font-weight: bold;
				&.num {
					font-size: 18px;
				}
				&.unit {
					font-size: 14px;
				}
			}
		}
	}
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.panel-body .chart-panel .chart-legend-wrap .chart-legend .legend-item .legend {
		font-size: 18px;
	}
}
</style>



